]);
// 添加列表
const onAddItem = () => {
  var order = Math.ceil(Math.random(1, 1000) * 100000000);
  form.region = "1025" + order;
  const nPrice = (form.price * form.number).toLocaleString();
  const fPrice = (form.price * 1).toLocaleString();
  newData.value.push({
    order: "1020" + order,
    name: form.name,
    state: "",
    price: "￥" + fPrice,
    number: form.number,
    nPrice: "￥" + nPrice,
  });
  calculate();
  //  onUpdated
};

// 分离计算
const calculate = () => {
  form.orderPrice = 0;
  var spacecheck = /\D/gi;
  for (const item of newData.value) {
    form.orderPrice += parseInt(item.nPrice.replace(spacecheck, ""));
    form.amount = form.orderPrice;
  }
};
