import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueAxios from 'vue-axios'
import axios from 'axios'
import './style/style.scss'
import ElementPlus from 'element-plus'// Vue３では、ElementPlus使い
import 'element-plus/dist/index.css'
import {Vuex} from 'vuex'

const app = createApp(App)
app.config.productionTip = false
// app.provide('$axios', axios)
// 全局注册组件
// Vue.component('my-header', [definition定义])

// createApp.use(Vuex)

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
app.config.globalProperties.$axios = axios
// axios.config.globalProperties.$axios = axios
// createApp.prototype.$axios = axios

// app.provide('$axios', axios)

// axios.defaults.baseURL = 'https:// api.example.com'
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN

// app.use(axios) // 告诉vue 要用这个插件

// this.$axios('http:// 20cm0128.main.jp/WebShop/getLearn.php')
axios('http://20cm0128.main.jp/WebShop/getLearn.php')
  .then(function (response) {
    // 处理成功情况
    console.log(response)
  })
  .catch(function (error) {
    // 处理错误情况
    console.log(error)
  })
  .then(function () {
    // 总是会执行
  })
app.use(router)
app.use(store)
app.use(Vuex)
//https://vuex.vuejs.org/zh/guide/#%E6%9C%80%E7%AE%80%E5%8D%95%E7%9A%84-store
//https://github.com/ddhjx-ai/notes/blob/58346f1ac42d456597a65ba4f7b8eae7ba2cb7e9/vue/vuex.md
//https://github.com/hasubasora/BananaFish/blob/master/src/main.js
app.use(VueAxios, axios)
app.provide('$axios', axios)
app.use(ElementPlus, { size: 'small', zIndex: 3000 })
app.mount('#app')
